.content {
  font-style: italic;
  font-size: 18px;
}

.content img {
  width: 100%;
}

.profileContent {
  display: flex;
  flex-wrap: wrap;
}

.descriptionBlock {
  max-width: 300px;
  padding: 10px;
  margin-left: 50px;
  margin-right: 50px;
  line-height: 30px;
}

.fullName {
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 40px;
}

.profileContent img {
  width: 300px;
}

.job {
  margin-top: 10px;
}

.job img {
  width: 40px;
  margin-left: 10px;
}

.contacts {
  font-weight: bold;
  font-size: 26px;
  color: #008080;
}

.contacts a{
  text-decoration: none;
  font-weight: 400;
  font-size: 18px;
  color: black;
}

.contacts img {
  width: 20px;
}

.contacts div {
  margin: 5px;
}